import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
const types = { 
	SET_LOCATION:'SET_LOCATION',
	SET_ADDRESS:'SET_ADDRESS',
	ORDER_INFO:'ORDER_INFO',
	USER_INFO:'USER_INFO',
	REMARK_INFO:'REMARK_INFO'
	
};
const state = {
	location:{},
	adderss:'',
	order_info:null,
	userInfo:null,
	remarkInfo:{
		tableware:'',
		remark:''
	}
};
const getters = {
	location: state => state.location,
	address: state => state.adderss,
	order_info: state => state.order_info,
	userInfo: state => state.userInfo,
	totalPrice:state => {
		let price = 0;
		if(state.order_info){
			const selectFoods = state.order_info.selectFoods
			selectFoods.forEach(food => {
				price += (food.activity.fixed_price * food.count);
			})
			price += state.order_info.shopInfo.float_delivery_fee;
			console.log(price)
		}
		return price;
	},
	remarkInfo:state => state.remarkInfo
};
const mutations = {
	[types.SET_LOCATION](state,location){
		if(location){
			state.location = location;
		}else{
			state.location = null;
		}
	},
	[types.SET_ADDRESS](state,adderss){
		if(adderss){
			state.adderss = adderss
		}else{
			state.adderss = "";
		}
	},
	[types.ORDER_INFO](state,order_info){
		if(order_info){
			state.order_info = order_info
		}else{
			state.order_info = null;
		}
	},
	[types.USER_INFO](state,userInfo){
		if(userInfo){
			state.userInfo = userInfo
		}else{
			state.userInfo = null;
		}
	},
	[types.REMARK_INFO](state,remarkInfo){
		if(remarkInfo){
			state.remarkInfo = remarkInfo
		}else{
			state.remarkInfo = '';
		}
	}
};
const actions = {
	setLocation:({commit},location)=>{
		commit(types.SET_LOCATION,location);
	},
	setAddress:({commit},adderss)=>{
		commit(types.SET_ADDRESS,adderss);
	},
	setOrder_info:({commit},order_info)=>{
		commit(types.ORDER_INFO,order_info);
	},
	setUser_info:({commit},userInfo)=>{
		commit(types.USER_INFO,userInfo);
	},
	setRemark_info:({commit},remarkInfo)=>{
		commit(types.REMARK_INFO,remarkInfo);
	}
}

export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})
