<template>
  <div class="search">
	  <!-- 目前只能搜索汉堡，别问为什么，因为我喜欢 -->
	  <Header :isLeft="true" title="搜索"></Header>
	  <div class="search_header">
		  <form action="" class="search_wrap">
			  <i class="fa fa-search"></i>
			  <input type="text" v-model="key_word" placeholder="商家信息">
			  <button @click.prevent="searchHandle">搜索</button>
		  </form>
		  <div class="shop" v-if="result && !showShop" >
			  <div class="empty_wrap" v-if="empty">
				  <div class="emoty_txt">
					  <h4>此搜索词未有结果</h4>
				  </div>
			  </div>
			  <div v-else>
				  <searchIndex @click="$router.push('/shop')" :data="result.restaurants"></searchIndex>
				  <searchIndex @click="shopItemClick" :data="result.words"></searchIndex>
			  </div>
		  </div>
		  <div class="container" v-if="showShop">
			  <FilterView
			  :filterDate="filterDate" 
			  @update="update"></FilterView>
			  <div class="shoplist"
			  v-infinite-scroll="loadMore"
			  :infinite-scroll-disabled="loading">
			  		  <IndexShow v-for="(item,index) in restaurants" :key="index"
			  		  :restaurant="item.restaurant"></IndexShow>
			  </div>
		  </div>
	  </div>
  </div>
</template>
<script>
import Header from '../components/Header.vue'
import searchIndex from '../components/searchIndex.vue'
import FilterView from '../components/FilterView.vue'
import IndexShow from '../components/IndexShow.vue'
	export default{
		name:"search",
		data(){
			return {
				key_word:"",
				result:null,
				empty:false,
				showShop:false,
				filterDate:[],
				page:0,
				size:7,
				restaurants:[],
				loading:false,
				data:null
			}
		},
		watch:{
			key_word(){
				this.empty = false;
				this.showShop = false;
				this.keyWordChang();
			}
		},
		created(){
			
			this.$axios("/api/profile/filter")
			.then(res=>{
				this.filterDate = res.data;
				// console.log(this.filterDate);
			});
		},
		methods:{
			keyWordChang(){
				this.$axios(`/api/profile/typeahead/${this.key_word}`)
				.then(res=>{
					this.result = res.data;
					console.log(res.data);
				})
				.catch(err=>{
					console.log(err);
					this.result = null;
				})
			},
			searchHandle(){
				if(!this.key_word) return;
				if(this.result && (this.result.restaurants.length > 0 || this.result.words > 0)){
					this.empty = false;
					this.shopItemClick();
				}else{
					this.empty = true;
				}
			},
			shopItemClick(){
				this.showShop = true;
				this.page = 0;
				this.restaurants = [];
				this.loading = false;
			},
			update(condition){
				// console.log(condition);
				this.data = condition;
				this.shopItemClick();
			},
			loadMore(){
				this.page++;
				this.$axios.post(`/api/profile/restaurants/${this.page}/${this.size}`,this.data)
				.then(res=>{
					// console.log(res.data);
					if(res.data.length > 0){
						res.data.forEach(res=>{
							this.restaurants.push(res);
						})}else{
							this.loading = true;
						}
					})
					
				}
			},
		components:{
			Header,
			searchIndex,
			FilterView,
			IndexShow
		}
	};
</script>
<style scoped>
.search {
  width: 100%;
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
}
.search_header {
  margin-top: 45px;
  background: #fff;
  padding: 0 4.266667vw;
}
.search_header .search_wrap {
  padding: 2.933333vw 2.933333vw 2.933333vw 0;
  display: flex;
  align-items: center;
  position: relative;
}
.search_wrap .fa-search {
  width: 2.933333vw;
  height: 2.933333vw;
  color: #888;
  position: absolute;
  top: 4.6666666vw;
  left: 2.933333vw;
}
.search_wrap input {
  flex-grow: 1;
  border-radius: 0.266667vw;
  background-color: #f8f8f8;
  padding: 1.733333vw 4vw 1.733333vw 8.8vw;
  color: #666;
  outline: none;
  border: none;
}
.search_wrap button {
  outline: none;
  border: none;
  color: #333;
  font-size: 0.43rem;
  background: #fff;
  font-weight: 500;
  margin-left: 2.933333vw;
  font-size: 14px;
}

.shop {
  width: 100%;
  height: calc(100% - 95px);
  overflow: auto;
}

.empty_wrap {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: #fff;
  display: flex;
  justify-content: center;
}
.empty_wrap img {
  width: 35vw;
  height: 35vw;
}
.empty_txt h4 {
  color: #666;
  font-size: 1rem;
  margin: 12vw 0 2vw 0;
}
.empty_txt span {
  color: #999;
  font-size: 0.8rem;
}
</style>
