<template>
  <div class="addAddress">
	  <Header :isLeft="true" :title="title"></Header>
	  <!-- 添加地址 -->
	  <div class="viewbody" v-show="!showSearch">
		  <div class="content">
			  <formBlock 
			  :tags="sexes" 
			  label="联系人" 
			  placeholder="姓名"
			  @checkSex="checkSex"
			  :sex="addressInfo.sex"
			  v-model="addressInfo.name"></formBlock>
			  
			  <formBlock label="电话" 
			  placeholder="手机号码"
			  v-model="addressInfo.phone"></formBlock>
			  
			  <formBlock label="地址" 
			  @click="showSearch=true" 
			  placeholder="小区/写字楼/学校等" 
			  icon="angle-right"
			  v-model="addressInfo.address"></formBlock>
			  
			  <formBlock textarea="textarea" 
			  label="门牌号" 
			  placeholder="10号楼5单元404" 
			  icon="edit"
			  v-model="addressInfo.bottom"></formBlock>
			  
			  <div class="formblock">
				  <div class="label-wrap">标签</div>
			  </div>
			  <tabTag :tags="tags" :selectTag="addressInfo.tag" @checkTag="checkTag"></tabTag>
		  </div>
		  <div class="form-button-wrap">
			  <button class="form-button" @click="handleSave">确定</button>
		  </div>
	  </div>
	  <!-- 搜索地址 -->
	  <addressSearch @close="showSearch = false" :addressInfo="addressInfo" :showSearch="showSearch"></addressSearch>
  </div>
</template>
<script>
import Header from '../../components/Header.vue'
import formBlock from '../../components/Orders/FormBlock.vue'
import tabTag from '../../components/Orders/TabTag.vue'
import addressSearch from '../../components/Orders/addressSearch.vue'
import { Toast } from 'mint-ui';
	export default{
		name:"addAddress",
		components:{
			Header,
			formBlock,
			tabTag,
			addressSearch
		},
		data(){
			return {
				title:'',
				tags:["家","学校","公司"],
				sexes:["先生","女士"],
				addressInfo:{
				},
				showSearch:false
			}
		},
		beforeRouteEnter(to,from,next){
			next(vm=>{
				vm.title = to.params.title;
				vm.addressInfo = to.params.addressInfo;
			})
		},
		methods:{
			checkTag(item){
				this.addressInfo.tag = item;
				// console.log(this.addressInfo);
			},
			checkSex(item){
				this.addressInfo.sex = item;
				// console.log(this.addressInfo);
			},
			handleSave(){
				if(!this.addressInfo.name){
					this.showMsg("请输入联系人");
					return;
				}
				if(!this.addressInfo.phone){
					this.showMsg("请输入电话");
					return;
				}
				if(!this.addressInfo.address){
					this.showMsg("请输入地址");
					return;
				}
				console.log(this.addressInfo);
				if(this.title == "添加地址"){
					this.addaddress();
				}else{
					this.editAddress();
				}
				
			},
			showMsg(msg){
				Toast({
				  message: msg,
				  position: 'bottom',
				  duration: 2000
				});
			},
			addaddress(){
				this.$axios.post(`/api/user/add_address/${localStorage.ele_login}`,this.addressInfo)
				.then(res=>{
					if(!this.$store.getters.userInfo){
						this.$store.dispatch("setUser_info",this.addressInfo);
					}
					this.$router.push('/myAddress')
				})
				.catch(err=>{
					console.log(err);
				})
			},
			editAddress(){
				this.$axios.post(`/api/user/edit_address/${localStorage.ele_login}/${this.addressInfo._id}`,
				this.addressInfo)
				.then(res=>{
					this.$router.push("/myAddress");
				})
			}
		}
	};
</script>
<style scoped>
.addAddress {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding-top: 45px;
}
.viewbody {
  width: 100%;
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
  background-color: #f5f5f5;
}

.content {
  padding-left: 4vw;
  background: #fff;
  font-size: 0.94rem;
}

.formblock {
  background-color: #fff;
  border-bottom: 1px solid #eee;
  display: flex;
}
.formblock .label-wrap {
  flex-basis: 17.333333vw;
  padding: 3.733333vw 0;
  line-height: 4.8vw;
  color: #333;
  font-weight: 700;
}

/* 确定按钮 */
.form-button-wrap {
  padding: 5.333333vw 4vw;
  display: flex;
}
.form-button-wrap .form-button {
  background: #00d762;
  text-align: center;
  border-radius: 0.533333vw;
  flex: 1;
  font-size: 1.1rem;
  line-height: 5.066667vw;
  color: #fff;
  padding: 3.333333vw 0;
  border: none;
  font-weight: 500;
}
</style>