<template>
  <div class="text_group">
	  <!-- 组件结构 -->
	  <!-- 组件容器 -->
	  <div class="input_group" :class="{'is-invalid':error}">
		  <input :type="type"
		  :value="value"
		  :placeholder="placeholder" 
		  :disabled="disabled"
		  :name="name"
		  @input="$emit('input',$event.target.value)"
		  >
		  <!-- 验证按钮 -->
		  <button v-if="btnTitle" :disabled="disabled"
		  @click="$emit('btnClick')">{{btnTitle}}</button>
	  </div>
	  <!-- 错误提醒 -->
	  <div class="invalid-feedback" v-if="error">{{error}}</div>
  </div>
</template>
<script>
	export default{
		name:"text_gruop",
		props:{
			type:{
				type:String,
				default:"text"
			},
			placeholder:String,
			disabled:Boolean,
			value:String,
			btnTitle:String,
			error:String,
			name:String
		}
	};
</script>
<style scoped>
.input_group {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.input_group input {
  height: 100%;
  width: 60%;
  border: 0px;
  outline: none;
}
.input_group button {
  border: none;
  outline: none;
  background: #fff;
}
.input_group button[disabled] {
  color: #aaa;
}
.is-invalid {
  border: 1px solid red;
}
.invalid-feedback {
  color: red;
  padding-top: 5px;
}
</style>
