<template>
  <div class="Rating-gray">
  		<i class="fa" v-for="(item,index) in itemClasses" :key="index" :class="item"></i>			  
  </div>
</template>
<script>
const lengths = 5;
const cls_on = "fa-star";
const cls_half = "fa-star-half-empty";
const cls_off = "fa-star-o";
export default{
		name:"Rating-gray",
		props:{
			rating:Number
		},
		computed:{
			itemClasses(){
				let result = [];
				let score = Math.floor(this.rating * 2) / 2;
				// 控制半星
				let hasDecisal = score % 1 !== 0;
				let integer = Math.floor(score);
				for(let i = 0;i<integer;i++){
					result.push(cls_on);
				}
				if(hasDecisal){
					result.push(cls_half);
				}
				//用于补齐
				while(result.length<lengths){
					result.push(cls_off);
				}
				return result;
			}
		}
	};
</script>
<style scoped="scoped">
.Rating-gray{
	margin-right: 1.066667vw;
	color: #ffbe00;
	display: inline-block;
}
</style>
