<template>
  <div class="login">
	  <div class="logo">
		  <img src="../assets/logo.jpg" alt="">
	  </div>
	  <!-- 手机号 -->
	  <InputGroup type="number" 
	   placeholder="手机号" 
	   name="mobile" 
	   v-model="phone" 
	   :btnTitle="btnTitle"
	   :disabled="disabled"
	   :error="errors.phone"
	   @btnClick = "getVerifyCode"></InputGroup>
	   <!-- 验证码 -->
	   <InputGroup type="number" 
	   placeholder="验证码" 
	   name="verify" 
	   v-model="verifyCode"
	   :error="errors.code"></InputGroup>
	   <!-- 用户服务协议 -->
	   <div class="login_des">
			<p>新用户登陆即自动注册，表示已同意
			<span>《用户服务协议》</span></p>
	   </div>
	   <!-- 登录按钮 -->
	   <div class="login_btn">
		   <button :disabled="isClick" @click="handleLogin">登陆</button>
	   </div>
  </div>
</template>
<script>
import InputGroup from "../components/inputGroup.vue"
import { Toast } from 'mint-ui';
	export default{
		name:"login",
		data(){
			return {
				phone:'',
				verifyCode:'',
				errors:{},
				btnTitle:'获取验证码',
				disabled:false
			}
		},
		computed:{
			isClick(){
				if(!this.phone || !this.verifyCode){
					return true;
				}else{
					return false;
				}
			}
		},
		methods:{
			handleLogin(){
				this.errors = {};
				this.$axios.post("/api/posts/sms_back",{
					phone:this.phone,
					code:this.verifyCode
				}).then(res=>{
					//检验成功，设置登录状态并且跳转到 / 页面
					localStorage.setItem("ele_login",res.data.user._id);
					this.$router.push("/");
				}).catch(err=>{
					this.errors = {
						code:err.response.data.msg
					};
				});
				// localStorage.setItem("ele_login",true);
				// this.$router.push("/");
			},
			getVerifyCode(){
				if(this.validatePhone()){
					this.$axios.post('/api/posts/sms_send',{
						// sid:"e9f0f9bac053e06c6d1eee12d18391e9",
						// token:"5ec5b8336f73e42715539efd7914a2f6",
						// appid:"004c33fdad4b4840a9b54aafd38bf233",
						// templateid:"533171",
						phone:this.phone
					}).then(res => {
						console.log(res);
						this.validateBtn(res.data);
					});
						// this.validateBtn();
				}
			},
			validateBtn(res){
				//发送网络请求，进行短信验证码的发送
				let time = 40;
				let timer = setInterval(()=>{
					if(time == 0){
						clearInterval(timer);
						this.btnTitle = "获取验证码";
						this.disabled = false;
					}else{
						this.disabled = true;
						this.btnTitle = time + "秒后重试";
						time--;
					}
				},1000);
				Toast({
				  message: '验证码为'+res.msg.substring(3,11),
				  position: 'bottom',
				  duration: 5000
				});
			},
			validatePhone(){
				if(!this.phone){
					this.errors = {
						phone:"手机号码不能为空"
					};
					return false;
				}else if(!/^1[345678]\d{9}$/.test(this.phone)){
					this.errors = {
						phone:'手机格式错误!'
					};
					return false;
				}
				else{
					this.errors = {};
					return true;
				}
			}
		},
		components:{
			InputGroup
		}
	};
</script>
<style scoped>
.login{
	width: 100%;
	height: 100%;
	padding: 30px;
	box-sizing: border-box;
	background-color: #fff;
}
.login{
	text-align: center;
}
.login img{
	width: 150px;
}
.text_group,
.login_des,
.login_btn{
	margin-top: 20px;
}
.login_des{
	color: #aaa;
	line-height: 22px;
}
.login_des span{
	color: #4d90fe;
}
.login_btn button{
	width: 100%;
	height: 40px;
	background-color: #48ca38;
	border-radius: 4px;
	color: #FFFFFF;
	font-size: 14px;
	border: none;
	online:none
}
.login_btn button[disabled] {
  background-color: #8bda81;
}
</style>
