<template>
  <div class="home">
	  <div class="header">
		  <div class="address_map" @click="$router.push({
			  name:'address',
			  params:{
				  city:city
			  }
		  })">
			  <i class="fa fa-map-marker"></i>
			  <span>{{address}}</span>
			  <i class="fa fa-sort-desc"></i>
		  </div>
		  
	  </div>
	  <div class="search_wrap" 
	  :class="{'fixedview':showFilter}"
	  @click="$router.push('/search')">
		  <div class="shop_search">
		  			  <i class="fa fa-search"></i>
		  			  搜索商家 商家名称
		  </div>
	  </div>
	  <div id="container">
		  <!-- 轮播图 -->
		  <mt-swipe 
		  :auto="4000" 
		  class="swiper">
		    <mt-swipe-item
			 v-for="(img,index) in swipeImgs" 
			 :key="index">
				<img :src="img" alt="">
			</mt-swipe-item>
		  </mt-swipe>
			  <mt-swipe 
			  :auto="0" 
			  class="entries">
			    <mt-swipe-item
			  			 v-for="(entrie,i) in entries" 
						 :key="i"
						 class="entry_wrap">
						 <div 
						 v-for="(item,index) in entrie" 
						 :key="index"
						 class="foodentry">
						 <div class="img_wrap">
							 <img :src="item.image">
						 </div>
							 <span>{{item.name}}</span>
						 </div>
			  			</mt-swipe-item>
			  </mt-swipe>
	  </div>
	  <!-- 推荐商家 -->
	  <div class="shoplist-title">推荐商家</div>
	  <!-- 导航栏 -->
	  <FilterView 
	  :filterDate="filterDate" 
	  @searchFixed="showFilterView"
	  @update="update"></FilterView>
	  <!-- 商家信息 -->
	  <mt-loadmore 
	  :top-method="loadTop" 
	  :bottom-method="loadBottom" 
	  :bottom-all-loaded="allLoaded" 
	  ref="loadmore"
	  :auto-fill="false"
	  :bottomPullText="bottomPullText"
	  :bottomDropText="bottomDropText"
	  :bottomLoadingText="bottomLoadingText">
	    <div class="shoplist">
	    		  <IndexShow v-for="(item,index) in restaurants" :key="index"
	    		  :restaurant="item.restaurant"></IndexShow>
	    </div>
	  </mt-loadmore>
	  
  </div>
</template>
<script>
import FilterView from '../components/FilterView.vue';
import IndexShow from '../components/IndexShow.vue';
	export default{
		name:"home",
		data(){
			return {
				swipeImgs:[],
				entries:[],
				filterDate:{},
				showFilter:false,
				page:1,
				size:5,
				restaurants:[],
				allLoaded:false,
				bottomPullText:"上拉加载",
				bottomDropText:"释放加载",
				bottomLoadingText:"加载中途",
				data:''
			}
		},
		components:{
			FilterView,
			IndexShow
		},
		computed:{
			address(){
			return 	this.$store.getters.address;
			},
			city(){
				return this.$store.getters.location.addressComponent.city || 
				this.$store.getters.location.addressComponent.province;
			}
		},
		created(){
			this.getDate();
		},
		methods:{
			getDate(){
				this.$axios("/api/profile/shopping")
				.then(res=>{
					// console.log(res.data);
					this.swipeImgs = res.data.swipeImgs;
					this.entries = res.data.entries;
				}).catch((err)=>{
					console.log("图片获取失败");
				});
				this.$axios("/api/profile/filter")
				.then(res=>{
					this.filterDate = res.data;
					// console.log(this.filterDate);
				});
				this.loadTop();
			
			},
			loadTop(){
				//拉取商家信息
				this.page = 1;
				this.allLoaded = false;
				this.bottomPullText = "上拉加载",
				this.$axios.post(`/api/profile/restaurants/${this.page}/${this.size}`,this.data)
				.then(res=>{
					// console.log(res.data);
					this.$refs.loadmore.onTopLoaded();
					this.restaurants = res.data;
				})
				// this.$refs.loadmore.onTopLoaded();
			},
			loadBottom(){
				if(!this.allLoaded){
					this.page++;
					this.$axios.post(`/api/profile/restaurants/${this.page}/${this.size}`)
					.then(res =>{
						// console.log(res.data);
						this.$refs.loadmore.onBottomLoaded();
						if(res.data.length > 0){
							res.data.forEach(res=>{
								this.restaurants.push(res);
							})
						}
						if(res.data < this.size){
							this.allLoaded = true;// 若数据已全部获取完毕
							this.bottomPullText = "没有更多数据";
						}
						else{
							this.allLoaded = true;
							this.bottomPullText = "没有更多数据";
						} 
					})
				}
				
			},
			showFilterView(isShow){
				this.showFilter = isShow;
			},
			update(condition){
				// console.log(condition);
				this.data = condition;
				this.loadTop()
			}
		}
	};
</script>
<style scoped="scoped">
.home {
  width: 100%;
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
}
.header,.search_wrap {
  background-color: #009eef;
  padding: 10px 16px;
}
.header .address_map {
  color: #fff;
  font-weight: bold;
}
.address_map i {
  margin: 0 3px;
  font-size: 18px;
}
.address_map span {
  display: inline-block;
  width: 80%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.search_wrap .shop_search {
  /* margin-top: 10px; */
  background-color: #fff;
  padding: 10px 0;
  border-radius: 4px;
  text-align: center;
  color: #aaa;
}
.search_wrap{
	position: sticky;
	top:0px;
	z-index: 999;
	box-sizing: border-box;
}
.swiper{
	height: 100px;
}
.swiper img{
	width: 100%;
	height: 100px;
}
.entries {
  background: #fff;
  height: 47.2vw;
  text-align: center;
  overflow: hidden;
}
.foodentry {
  width: 20%;
  float: left;
  position: relative;
  margin-top: 2.933333vw;
}
.foodentry .img_wrap {
  position: relative;
  display: inline-block;
  width: 12vw;
  height: 12vw;
}
.img_wrap img {
  width: 100%;
  height: 100%;
}
.foodentry span {
  display: block;
  color: #666;
  font-size: 0.32rem;
}
/* 推荐商家 */
.shoplist-title {
  display: flex;
  align-items: flex;
  justify-content: center;
  height: 9.6vw;
  line-height: 9.6vw;
  font-size: 13px;
  color: #333;
  background: #fff;
}
.shoplist-title:after,
.shoplist-title:before {
  display: block;
  content: "一";
  width: 5.333333vw;
  height: 0.266667vw;
  color: #999;
}
.shoplist-title:before {
  margin-right: 3.466667vw;
}
.shoplist-title:after {
  margin-left: 3.466667vw;
}

.fixedview {
  width: 100%;
  position: fixed;
  top: 0px;
  z-index: 999;
}

.mint-loadmore {
  height: calc(100% - 95px);
  overflow: auto;
}
</style>
