import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/index.vue'
import Login from '../views/login.vue'
import Home from '../views/Home.vue'
import Order from '../views/Order.vue'
import Me from '../views/Me.vue'
import address from '../views/address.vue'
import city from '../views/City.vue'
import search from '../views/Search.vue'
import shop from '../views/shops/shop.vue'
import goods from '../views/shops/Goods.vue'
import comments from '../views/shops/Comments.vue'
import seller from '../views/shops/Seller.vue'
import myAddress from '../views/Orders/myAddress.vue'
import addAddress from '../views/Orders/addAddress.vue'
import Settlement from '../views/Orders/Settlement.vue'
import Remark from '../views/Orders/Remark.vue'
import Pay from '../views/Orders/Pay.vue'
import orderInfo from '../views/Orders/orderInfo.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    // name: 'index',
    component: Index,
	children:[
		{
			path:'/',
			redirect:'/home',
			//用于重定向 /会定向于/home中
		},
		{
			name:'home',
			path:'/home',
			component:Home
		},
		{
			name:'Order',
			path:'/order',
			component:Order
		},
		{
			name:'Me',
			path:'/me',
			component:Me
		},
		{
			name:'address',
			path:'/address',
			component:address
		},
		{
			name:'city',
			path:'/city',
			component:city
		},{
			name:'search',
			path:'/search',
			component:search
		},
		{
			name:'shop',
			path:'/shop',
			component:shop,
			redirect:'/goods',
			children:[
				{
					name:'goods',
					path:'/goods',
					component:goods
				},
				{
					name:'comments',
					path:'/comments',
					component:comments
				},
				{
					name:'seller',
					path:'/seller',
					component:seller
				},
			]
		},
	],
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
			name:'myAddress',
			path:'/myAddress',
			component:myAddress
		},
		{
			name:'addAddress',
			path:'/addAddress',
			component:addAddress
		},
		{
					name:'Settlement',
					path:'/Settlement',
					component:Settlement
		},
		{
			name:'Remark',
			path:'/Remark',
			component:Remark
		},
		{
			name:'Pay',
			path:'/Pay',
			component:Pay
		},
		{
			name:'orderInfo',
			path:'/orderInfo',
			component:orderInfo
		}
		
  // {
  //   path: '/login',
  //   name: 'login',
  //   component: ()=> import("../views/login.vue")
  // }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  linkActiveClass:"active",
  routes
})
//路由守卫
router.beforeEach((to,from,next)=>{
	const isLogin = localStorage.ele_login ? true : false;
	if(to.path == "/login"){
		next();//正常跳转
	}else{
		//是否在登录状态下
		isLogin?next():next("/login");
	}
})

export default router